@use 'base/variables' as v;
@use 'base/mixins' as m;

.navegacion {
    background-color: v.$primario;
    padding: .3rem 0;
    margin: 2rem 0;

    &__border {
        border-top: 2px dashed v.$blanco;
        border-bottom: 2px dashed v.$blanco;
        padding: 1.4rem 0;
    }

    &__contenedor {
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 2rem;

        @include m.tablet {
            flex-direction: row;
            justify-content: space-between;
        }
    }

    &__enlace {
        color: v.$blanco;
        font-size: 2.4rem;
    }
}